.App {
  min-height: 100vh;
}

.bg-primary {
  background-color: #124A8B !important;
}

.right-0 {
  right: 0;
}

.hover-shadow, .cursor-pointer {
  cursor: pointer;
}

.hover-shadow:hover{
  box-shadow:0 .5rem 1rem rgba(0,0,0,.15) !important;
}

.no-text-decor{
  text-decoration: none;
  color: inherit;
  cursor: pointer !important;
}

.no-text-decor:hover{
  text-decoration: none;
  color: inherit;
}

.img-object-fit-cover {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.rounded-right-0 {
  border-top-right-radius:0 !important;
  border-bottom-right-radius: 0 !important;
}

.form-control:-webkit-autofill {
  -webkit-background-clip: text;
}

.fs-12 {
  font-size: 12px;
}

.fs-1_6vw {
  font-size: 1.6vw;
}

.fs-2_5vw {
  font-size: 2.5vw;
}

.p-1_5vw {
  padding: 1.5vw;
}

.pt-5p {
  padding-top: 5%;
}

.pb-10p {
  padding-bottom: 10%;
}

.bg-light-green {
  background-color: rgba(197, 229, 51, 1);
}

